
.layoutGame{
   height: 85vh;
   display: flex;
   justify-content: center;
   align-items: center;
}

.return{
   position: absolute;
   left: 4%;
   top: 15%;
   display: flex;
   justify-content: center;
   align-items: center;
   opacity: 1;
   font-size: 1.3vw;
   transition: all 0.2s;
}
.return:hover{
   opacity: 0.5;

}
.return > img{
   width: 2vw;
}
.return > span{
   color: white;
   text-decoration:none;
}

@media (max-width:500px){
   .return{
      font-size: 6vw;
      gap: 1vw;
   }
   .return >img{
      width: 6vw;
   }
}