.container{
    position: absolute;
    width: 20vw;
    height: 15vw;
    left: 73vw;
    background-color: #92c3e3c9;
    backdrop-filter: blur(6px);
    display: none;
    flex-direction: column;
    align-items: center;
    border-radius: 1vw;
}
.container > h1{
    color: white;
}
.container div > p{
    color: white;
    font-weight: bold;
}

.containerButtons{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 3vw;
}
.containerButtons > div{
    width: 2vw;
    height: 2vw;
    padding: 0.3vw;
    background-color: white;
    border-radius: 10vw;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    transition: all 0.3s;
}
.containerButtons > div:hover{
    transform: rotate(90deg);
}
.containerButtons > div > img{
    width: 2.6vw;
}
.containerHome{
    width: 2.6vw;
    height: 2.6vw;
    background-color: white;
    border-radius: 10vw;
    display: flex;
    justify-content: center;
    align-items: center;
}
.containerHome > img{
    width: 2.6vw;

}


@media (max-width:500px){
    .container{
        width: 80vw;
        height: 60vw;
        left: 10%;
    }
    .container > h1{
        font-size: 8vw;
    }
    .container>div>p{
        font-size: 7vw;
        font-weight: 400;
    }
    
    .containerButtons{
        gap: 20vw;
    }
    .containerButtons>div>img{
        width: 9vw;
    }
        .containerButtons > div{
            width: 9vw;
            height: 9vw;
        }
        .containerHome{
            width: 9vw;
            height: 9vw;
        }
        .containerHome > img{
            width: 9vw;
            height: 9vw;
        }
}