.containerCubes{
    display: flex;
    gap: 3vw;
}
.containerGame{
   display: flex;
   flex-direction: column;
   justify-content: center;
   align-items: center;
   gap:3vw;
   width: 100%;
   height: 100vh;
   
}
.buttonStart{
    background-color: rgba(255, 255, 255, 0.792);
    justify-self: center;
    font-size: 3vw;
    border: none;
    border-radius: 1vw;
    cursor: pointer;
}
.buttonStart:hover{
    color: rgb(233, 166, 33);
}

.containerDisplaySelectCube{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 4vw;
}
.containerDisplaySelectCube > button{
    cursor: pointer;
}

.triangleLeft{
    width: 6vw;
    cursor: pointer;
    transition: all 0.3s;
}

.triangleRight{
    width: 6vw;    
    cursor: pointer;
    transform: rotate(180deg);
}


@media (max-width:500px){
    .containerDisplaySelectCube{
        flex-direction: row;
        gap: 10vw;
    }
    .containerCubes{
        flex-direction: column;
        gap: 8vw;
    }
    .triangleLeft{
        width: 16vw;

        transform: rotate(90deg);
    }
    .triangleRight{
        width: 16vw;

        border-top: 40px solid transparent;
        transform: rotate(-90deg);
    }
    .buttonStart{
        margin-top: 10vw;
        width: 40vw;
        height: 10vw;
        font-size: 6vw;
        font-weight: bold;
        color: rgba(0, 0, 0, 0.628);
    }
}