.type4{
    height: 15vw;
    width: 15vw;
}
.type12{
    height: 10vw;
    width: 10vw;
}
.type12 > img{
    width: 8.5vw;
}
.type16{
    height: 8vw;
    width: 8vw;
}
.type16 > img{
    width: 7vw;
}

@media (max-width:500px) {
    .type4{
        width: 30vw;
        height: 30vw;
    }
    .type4 > img{
        width: 26vw;
    }

    .type12{
        width: 22vw;
        height: 22vw;
    }
    .type12>img{
        width: 18vw;
    }

    .type16{
        width: 16vw;
        height: 16vw;
    }
    .type16>img{
        width: 14vw;
    }
}

/* efectos de las imagenes */
.hiddeEffect{
    transform: rotateY(0deg);
    animation: hiddenEff 0.5s ease-in 1;
}
@keyframes hiddenEff {
    from{ transform: rotateY(180deg);}
    to{
        transform: rotateY(0deg);
    }
}


.flipEffect{
    transform: rotateY(180deg);
    animation: comprobationContainer  0.5s ease-in 1 ;
}
.matchPair{
    transform: rotateY(180deg);
    cursor: default;
}

@keyframes comprobationContainer {
    from{
        transform: rotateY(0deg) ;
    }to{
        transform: rotateY(180deg) ;

    }
}