.container{
    position: absolute;
    width: 20vw;
    height: 15vw;
    border-radius: 1vw;
    background-color: rgb(247, 247, 247);

    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.containerButtons{
    display: flex;
    gap: .5vw;
}
.categorieButton{
    background-color:#96C5E3;
    padding: .3vw;
    text-decoration: none;
    color: rgb(0, 83, 132);
    border-radius: .3vw;
}
.categorieButton:hover{
    color: #96C5E3;
    background-color:rgb(0, 83, 132);
}


.containerButton{
    justify-content: end;

}
.closeButton{
    width: 2vw;
    cursor: pointer;
    justify-self: end;
}

@media (max-width:500px) {
    .container{
        width: 70vw;
        height: 60vw; 
        border-radius: 4vw;  
    }
    .closeButton{
        width: 7vw;
    }
    .containerButtons{
        gap: 7vw;
    }
}