.containerButtons{
    background-color: orange;
    border-radius: 0.3vw;
    width: 3vw;
    height: 3vw;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    margin-top: 2vw;
}
.containerButtons > img{
    width: 2vw;
}

@media (max-width:500px) {
    .containerButtons{
        width: 10vw;
        height: 10vw;
    }
    .containerButtons > img{
        width: 8vw;
    }
}