.square4{
    width: 7vw;
    height: 7vw;
    background: rgba(255, 255, 255, 0.798);
    border-radius: 0.6vw;
}.square12{
    width: 4.13vw;
    height: 4vw;
    background: rgba(255, 255, 255, 0.798);
    border-radius: 0.6vw;
}.square16{
    width: 4vw;
    height: 4vw;
    background: rgba(255, 255, 255, 0.798);
    border-radius: 0.6vw;
}

@media (max-width:500px) {
    .square4{
        width: 15vw;
        height: 15vw;
        background: rgba(255, 255, 255, 0.798);
        border-radius: 0.6vw;
    }.square12{
        width: 9vw;
        height: 9vw;
        background: rgba(255, 255, 255, 0.798);
        border-radius: 0.6vw;
    }.square16{
        width: 8vw;
        height: 8vw;
        background: rgba(255, 255, 255, 0.798);
        border-radius: 0.6vw;
    }
}

@keyframes scaleTransitionBig {
    from{
        box-shadow: none;
        transform: scale(1);
        opacity: 0.7;
    }to{
        box-shadow: 2px 2px 21px 0px rgba(0,0,0,0.75);
        transform: scale(1.1);
        opacity: 1;
    }
}
@keyframes scaleTransitionLow {
    from{
        box-shadow: 2px 2px 21px 0px rgba(0,0,0,0.75);
        transform: scale(1.1);
        opacity: 1;

    }to{
        box-shadow: none;
        transform: scale(1);
        opacity: 0.7;

    }
}