.containerSelector{
    width: 18vw;
    height: 18vw;
    border-radius: 1vw;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    gap: .5vw;
    backdrop-filter: blur(6px);
    border: 1px rgba(240, 248, 255, 0.412) solid;
}

@media (max-width:500px) {
    .containerSelector{
        width: 40vw;
        height: 40vw;
    }
}