.firstLayer{
    width: 100vw;
}

.secondLayer{
    width: 100vw;
    position: relative;
    z-index: -1;
}
.thirdLayer{
    width: 100vw;
    z-index: -1;
}
.fourLayer{
    width: 100vw;
    z-index: -1;
}
.fiveLayer{
    width: 100vw;
    z-index: -1;
}
.sixLayer{
    width: 100vw;
    z-index: -2;
}
.sevenLayer{
    width: 100vw;
    z-index: -2;
}

@media (max-width:768px) {
    .firstLayer{
        justify-self: center;
        width: 1200px;
    }
    .secondLayer{
        width: 1px;
        position: relative;
        z-index: -1;
    }
    .thirdLayer{
        width: 2000px;
        z-index: -1;
    }
    .sixLayer{
        width:2000px;
        z-index: -2;
    }
    
}
@media (max-width:500px) {
    .firstLayer{
        width: 1600px;
    }
    .secondLayer{
        width: 1px;
        position: relative;
        z-index: -1;
    }
    .thirdLayer{
        width: 1700px;
        z-index: -1;
    }
    .sixLayer{
        width:1700px;
        z-index: -2;
    }
    
}