
.cubeImage{
    width: 15vw;
    visibility: visible;
}
.container{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    width: 35vw;
    gap: 1vw;
    align-self: center;
    justify-self: center;
}



/* Effectos del cubo contenedor de las imagenes */
.containerCube{
    background-color: rgba(255, 255, 255, 0.739);
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    border-radius: 1vw;
    transform-style: preserve-3d;
    
}
.containerCube:disabled > div > img{
    filter: grayscale(80%);
    animation: disabledEffect 1s ease-in 1;
}
@keyframes disabledEffect {
    from{filter: grayscale(0%);} to{ filter: grayscale(80%);}
    
}


.back {
position: absolute;
width: 100%;
height: 100%;
backface-visibility: hidden;
display: flex;
align-items: center;
justify-content: center;
font-size: 5rem;
transform: rotateY(180deg);

}
.front {
background-color: #ff0000;
color: #fff;
    
}

@media (max-width:500px) {
    .container{
        width: 70vw;
    }
}
